import { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import data from "./../Film";

function Detail() {
    const { id } = useParams(); // router  .
    const [movieData, setMovieData] = useState([]);
    useEffect(() => {
        const json = data;
        const selectMovie = json.find((el) => el.Title === id);

        console.log(id);
        setMovieData(selectMovie);
    });

    return <h1>Detail</h1>;
}
export default Detail;
