import { useState, useEffect } from "react";
import Movie from "./../components/Movie";
import data from "./../Film";

function Home() {
    const [loading, setLoading] = useState(true);

    const [movies, setMovies] = useState([]);
    const getMovies = async () => {
        //const res = await fetch(`https://yts.mx/api/v2/list_movies.json?minimum_rating=8.8&sort_by=year`);
        // const res = await fetch(`file:///D:/WorkSpace/react-beginner/Film.json`);
        // const json = await res.json();
        const json = data;

        setMovies(json);
        setLoading(false);
    };
    useEffect(() => {
        getMovies();
    }, []);

    console.log(movies);
    return (
        <div>
            {loading ? (
                <h1>Loading...</h1>
            ) : (
                <div>
                    {movies.map((movie) => (
                        <Movie key={movie.Title} coverImg={movie.Images[0]} title={movie.Title} summary={movie.Plot} />
                    ))}
                </div>
            )}
        </div>
    );
}

export default Home;
