import { Link } from "react-router-dom";
const Movie = ({ coverImg, title, summary }) => {
    return (
        <div>
            <img src={coverImg} alt={title} />
            <h2>
                <Link to={`/movie/${title}`}>{title}</Link>
            </h2>
            <p>{summary}</p>
        </div>
    );
};

export default Movie;
